input {
    width: 80%;
    height: 40px;
    border: none;
    box-shadow: 2px 4px 8px rgba(0, 0, 0, 0.5);
    border-radius: 5px;
  }
  .input-wrap {
    text-align: center;
    padding: 20px 0;
  }
  .input-wrap h1 {
    margin-bottom: 20px;
  }
  @media screen and (max-width: 600px) {
    .data-wrapper {
      display: grid;
      grid-template-columns: repeat(1, 1fr);
    }
  }
  @media screen and (max-width: 768px) and (min-width: 600px) {
    .data-wrapper {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
    }
  }