@import 'material-icons/iconfont/material-icons.css';

.h1-btn {
    margin-top: 10px;
    margin-left: 30px;
    display: flex;
    gap: 20px;
  }
  img {
    width: 100%;
    object-fit: cover;
    border-radius: 30px;
  }
  .img-container {
    width: 400px;
    height: 400px;
  }
  .content {
    display: flex;
    gap: 20px;
    margin: 20px 20px;
  }
  .btn-wrapper {
    display: flex;
    gap: 21px;
    padding: 10px 30px;
  }
  .btn-wrapper button {
    padding: 5px 10px;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  .btn-active {
    background-color: #f2672c;
    color: white;
  }
  
  .inactive {
    display: none;
  }
  .body {
    padding: 20px 10px;
  }
  .info {
    margin-left: 30px;
  }
  .back-btn {
    background: none;
    border: none;
    cursor: pointer;
  }